class TACGenerator {
    constructor() {
        this.tempCount = 1;
        this.address = 100;
        this.tacLines = [];
        this.stack = [];
    }

    generateTAC(code) {
        this.tempCount = 1;
        this.address = 100;
        this.tacLines = [];
        this.stack = [];

        const lines = code.split("\n").map(line => line.trim()).filter(line => line);
        
        for (let i = 0; i < lines.length; i++) {
            const line = lines[i];
            
            if (line.startsWith("for")) {
                this.handleForLoop(line);
            } 
            else if (line.startsWith("if")) {
                this.handleIfStatement(line);
            } 
            else if (line.startsWith("else")) {
                this.handleElseStatement();
            } 
            else if (line === "}") {
                this.handleBlockEnd();
            } 
            else if (line.includes("=")) {
                this.handleAssignment(line);
            }
        }

        return this.tacLines.join("\n");
    }

    handleForLoop(line) {
        const iterator = line.match(/\((.*?)\)/)?.[1];
        const [init, condition, increment] = iterator.split(";").map(s => s.trim());
        
        // Handle initialization
        if (init) {
            this.tacLines.push(`${this.address++}: ${init}`);
        }
        
        const loopStart = this.address;
        const temp = `t${this.tempCount++}`;
        this.tacLines.push(`${this.address++}: ${temp} = ${condition}`);
        const exitJump = this.address++;
        this.tacLines.push(`${exitJump}: ifFalse ${temp} goto ?`);
        
        this.stack.push({
            type: 'for',
            exitJump,
            loopStart,
            increment,
            temp,
            startAddress: loopStart
        });
    }

    handleIfStatement(line) {
        const condition = line.match(/\((.*?)\)/)?.[1];
        const temp = `t${this.tempCount++}`;
        this.tacLines.push(`${this.address++}: ${temp} = ${condition}`);
        const elseJump = this.address++;
        this.tacLines.push(`${elseJump}: ifFalse ${temp} goto ?`);
        
        this.stack.push({
            type: 'if',
            elseJump,
            temp,
            startAddress: this.address
        });
    }

    handleElseStatement() {
        const prev = this.stack.pop();
        if (prev && prev.type === 'if') {
            const endIfJump = this.address++;
            this.tacLines.push(`${endIfJump}: goto ?`);
            this.tacLines[prev.elseJump] = `${prev.elseJump}: ifFalse ${prev.temp} goto ${this.address}`;
            this.stack.push({
                type: 'else',
                endIfJump,
                startAddress: this.address
            });
        }
    }

    handleBlockEnd() {
        const prev = this.stack.pop();
        if (!prev) return;

        switch (prev.type) {
            case 'for':
                if (prev.increment) {
                    this.tacLines.push(`${this.address++}: ${prev.increment}`);
                }
                this.tacLines.push(`${this.address++}: goto ${prev.loopStart}`);
                this.tacLines[prev.exitJump] = `${prev.exitJump}: ifFalse ${prev.temp} goto ${this.address}`;
                break;
            case 'else':
                this.tacLines[prev.endIfJump] = `${prev.endIfJump}: goto ${this.address}`;
                break;
            case 'if':
                this.tacLines[prev.elseJump] = `${prev.elseJump}: ifFalse ${prev.temp} goto ${this.address}`;
                break;
        }
    }

    handleAssignment(line) {
        const [varName, expr] = line.split("=").map(x => x.trim());
        if (!expr) return;

        // Handle complex expressions
        const parts = expr.split(/([+\-*/])/).map(p => p.trim()).filter(p => p);
        if (parts.length > 1) {
            // Handle binary operations
            let result = parts[0];
            for (let j = 1; j < parts.length; j += 2) {
                const op = parts[j];
                const right = parts[j + 1];
                const temp = `t${this.tempCount++}`;
                this.tacLines.push(`${this.address++}: ${temp} = ${result} ${op} ${right}`);
                result = temp;
            }
            this.tacLines.push(`${this.address++}: ${varName} = ${result}`);
        } else {
            // Handle simple assignment
            const temp = `t${this.tempCount++}`;
            this.tacLines.push(`${this.address++}: ${temp} = ${expr}`);
            this.tacLines.push(`${this.address++}: ${varName} = ${temp}`);
        }
    }
}

module.exports = TACGenerator; 